<template>
  <div class="categories__column">
    <router-link
      :to="'/catalog/' + catalogs.path + '/' + categories_data.categpath"
    >
      <div class="categories__item categ-block">
        <div class="categ-block__img">
          <img
            :src="require('../../assets/img/' + categories_data.image)"
            alt="img"
          />
          <div class="categ-block__btn btn">
            <p>{{ categories_data.name }}</p>
          </div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "categories-item",
  props: {
    categories_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    catalogs() {
      return this.$store.getters.CatalogPath(this.$route.params.path);
    },
  },
};
</script>

