        let data = axios.post("", {
          name: name,
          surn: surn,
          phone: phone,
          city: city,
          region: region,
          street: street,
          house: house,
          struct: struct,
          housing: housing,
          porch: porch,
          floor: floor,
          apartment: apartment,
        });
        if (data.status == "success") {
          console.log("Data deliveried");
        } else {
          throw new Error(data.status);
        }
      } catch (e) {
        this.error = e;
      }
    },
  },
};
