<template>
  <div class="cart">
    <div class="cart__block">
      <div class="cart__title">
        <h1>Корзина</h1>
      </div>
      <div class="cart__close" @click="closecart">
        <img src="../../assets/img/cross2.png" alt="" />
      </div>
    </div>

    <CartItem
      v-for="(item, index) in cart_data"
      :key="item.article"
      v-bind:cart_item_data="item"
      @deleteCart="deleteCart(index)"
    />
    <div class="cart__price">
      <p>Общая стоимость: {{ cartTotalCost }} Рублей <span></span></p>
    </div>

    <router-link :to="{ name: name }">
      <button class="cart__btn">Оформление заказа</button>
    </router-link>
  </div>
</template>

<script>
import CartItem from "@/components/cart/cart-item.vue";
import { mapActions } from "vuex";
export default {
  name: "cart",
  data() {
    return {
      name: "order",
    };
  },
  props: {
    cart_data: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    ...mapActions(["DELETE_FROM_CART"]),
    deleteCart(index) {
      this.$emit("deleteCart");
      // console.log(index);
      // this.DELETE_FROM_CART(index);
    },
    closecart() {
      this.$emit("closecart");
    },
    //  ShowCart() {
    //    if (this.show) {
    //      this.show = false;
    //    } else this.show = true;
    //  },
  },
  computed: {
    cartTotalCost() {
      let result = [];
      for (let item of this.cart_data) {
        result.push(item.price * item.section);
      }
      result = result.reduce(function (sum, el) {
        return sum + el;
      });
      return result;
    },
  },

  components: {
    CartItem,
  },
};
// v-for="(item, index) in cart_data"
// :key="item.id"
</script>

