<template>
  <div class="content">
    <div class="product">
      <div class="container">
        <div class="breadcrumbs">
          <router-link
            :to="'/catalog/' + catalogs.path + '/' + category.categpath"
            >Вернуться к каталогу</router-link
          >
        </div>

        <div class="product__row">
          <div
            class="product__columns"
            v-for="product in PRODUCTID"
            :key="product.id"
          >
            <Cart v-if="$route.meta.showCompPage" />
            <div class="product__img">
              <img src="../../assets/img/product1.png" alt="" />
            </div>
            <div class="product__info">
              <div class="product__name">
                <p>{{ product.name }}</p>
              </div>
              <div class="product__price">
                <p>
                  Цена: <span>{{ product.price }} рублей</span>
                </p>
              </div>
              <div class="product__description">
                <p>Описание:</p>
                <span>
                  {{ product.description }}
                </span>
              </div>
            </div>
            <div class="product__choice form">
              <div class="form__cross">
                <p>Цвет перекладины:</p>
                <div class="form__input">
                  <template v-for="color in product.formCross" :key="color.id">
                    <label class="checkbox-other">
                      <input
                        type="checkbox"
                        id="checkbox-id"
                        v-bind:value="color.colorCross"
                        v-model="selectedColorCross"
                      />
                      <span for="checkbox-id" class="span"></span>
                    </label>
                  </template>
                </div>
              </div>

              <div class="form__barr">
                <div class="form__cross">
                  <p>Цвет штанкетки:</p>
                  <div class="form__input">
                    <template
                      v-for="color in product.formBarrels"
                      :key="color.id"
                    >
                      <label class="checkbox-other">
                        <input
                          type="checkbox"
                          id="checkbox-id"
                          v-bind:value="color.colorBarrels"
                          v-model="selectedColorBarrels"
                        />
                        <span for="checkbox-id" class="span"></span>
                      </label>
                    </template>
                  </div>
                </div>
              </div>
              <div class="form__section">
                <p>Кол-во секций:</p>
                <div class="input-number">
                  <div class="input-number__minus" id="inputm" @click="minus">
                    -
                  </div>
                  <input
                    class="input-number__input"
                    type="text"
                    pattern="^[0-9]+$"
                    id="input"
                    v-model="section"
                  />
                  <div class="input-number__plus" id="inputp" @click="plus">
                    +
                  </div>
                </div>
              </div>
              <div class="form__row">
                <p>Столбы:</p>
                <label class="checkbox-other">
                  <input
                    type="checkbox"
                    id="checkbox-id"
                    value="false"
                    v-model="selectedRow"
                  />
                  <span for="checkbox-id" class="row-span"></span>
                </label>
              </div>
              <div class="form__colorrow">
                <div class="form__cross">
                  <p>Цвет столбов:</p>
                  <div class="form__input">
                    <template v-for="color in product.formRow" :key="color.id">
                      <label class="checkbox-other">
                        <input
                          type="checkbox"
                          id="checkbox-id"
                          v-bind:value="color.colorRow"
                          v-model="selectedColorRow"
                        />
                        <span for="checkbox-id" class="span"></span>
                      </label>
                    </template>
                  </div>
                </div>
              </div>
              <div class="form__btn">
                <input
                  type="button"
                  value="Добавить в корзину"
                  @click="addtoCart(product)"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Products from "@/components/products/products.vue";
import Cart from "@/components/cart/cart.vue";
import { mapActions, mapGetters } from "vuex";
import axios from "axios";
export default {
  name: "product-list",
  data() {
    return {
      show: true,
      cart: [],
      section: "",
      selectedColorCross: [],
      selectedColorBarrels: [],
      selectedColorRow: [],
      selectedRow: "",
    };
  },
  components: {
    Products,
    Cart,
  },
  created() {
    axios
      .get(
        `https://6578b14bf930.ngrok.io/api/catalog/${this.$route.params.path}/${this.$route.params.categpath}/${this.$route.params.id}`,
        //   "https://6578b14bf930.ngrok.io/api/catalog/" +
        //     this.catalogs.path +
        //     "/" +
        //     this.categories.categpath,
        {}
      )
      .then((response) => {
        (this.product = response.data),
          this.$store.dispatch("GET_PRODUCTITEM", this.product);
      })
      .catch((error) => {
        console.log(error);
        return error;
      });
  },
  methods: {
    ...mapActions(["GET_PRODUCTITEM"]),
    ...mapActions(["ADD_TO_CART"]),
    ...mapActions(["DELETE_FROM_CART"]),
    deleteCart(index) {
      this.cart.splice(index, 1);
      console.log("я сработал");
      // this.DELETE_FROM_CART(index);
    },
    minus() {
      let input = document.getElementById("input");
      let total = document.getElementById("input").previousElementSibling;
      if (input.value > 1) {
        return input.value--;
      }
    },
    plus() {
      let input = document.getElementById("input");
      let total = document.getElementById("input").nextElementSibling;
      return input.value++;

      // total.value(+total.value + 1);
    },
    addtoCart(product) {
      // this.cart.splice(product, 1);
      this.BEFORECART.push({
        name: product.name,
        price: product.price,
        ColorCross: this.selectedColorCross,
        ColorBarrels: this.selectedColorBarrels,
        ColorRow: this.selectedColorRow,
        selectedRow: this.selectedRow,
        section: this.section,
      }),
        console.log(this.BEFORECART);
      // this.$store.dispatch("GET_CART", this.CART);
      // console.log(this.CART);
      this.ADD_TO_CART(this.BEFORECART);

      // this.cart = [];
    },
    closecart() {
      if (this.show) {
        this.show = false;
      } else this.show = true;
    },
  },
  computed: {
    ...mapGetters(["PRODUCTID"]),
    ...mapGetters(["CART"]),
    ...mapGetters(["BEFORECART"]),
    catalogs() {
      return this.$store.getters.CatalogPath(this.$route.params.path);
    },
    category() {
      return this.$store.getters.CategoriesPath(this.$route.params.categpath);
    },
    products() {
      return this.$store.getters.ProductId(this.$route.params.id);
    },
  },
};
</script>

<style lang="sass" scoped>
.breadcrumbs
  margin: 0px 0px 15px 0px
</style>